'use client';

import { useDAWStore } from '@/store/useDAWStore';
import { Slider } from '@/components/ui/Slider';
import { Button } from '@/components/ui/Button';
import { VUMeter } from './VUMeter';
import { Knob } from './Knob';

interface FaderProps {
    id: string;
}

export function Fader({ id }: FaderProps) {
    const fader = useDAWStore((state) => state.faders.find((f) => f.id === id));
    const setFaderValue = useDAWStore((state) => state.setFaderValue);
    const setPan = useDAWStore((state) => state.setPan);
    const toggleSolo = useDAWStore((state) => state.toggleSolo);
    const toggleMute = useDAWStore((state) => state.toggleMute);

    if (!fader) return null;

    return (
        <div className="flex flex-col items-center gap-4 p-4 rounded-xl glass-panel w-28 transition-transform">
            {/* Channel Label */}
            <div className="text-xs font-bold text-silver-dark tracking-widest">{fader.label}</div>

            {/* Pan Knob */}
            <div className="flex flex-col items-center gap-1">
                <Knob
                    value={fader.pan}
                    min={-64}
                    max={63}
                    onChange={(val) => setPan(id, val)}
                />
                <span className="text-[10px] text-silver-dark">PAN {fader.pan > 0 ? `R${fader.pan}` : fader.pan < 0 ? `L${Math.abs(fader.pan)}` : 'C'}</span>
            </div>

            <div className="flex gap-2 w-full mt-2">
                <Button
                    size="sm"
                    className="flex-1 text-[10px] h-6 transition-colors"
                    variant={fader.isSoloed ? 'active' : 'outline'}
                    onClick={() => toggleSolo(id)}
                >
                    S
                </Button>
                <Button
                    size="sm"
                    className="flex-1 text-[10px] h-6 hover:bg-red-900/40 transition-colors"
                    variant={fader.isMuted ? 'active' : 'outline'}
                    onClick={() => toggleMute(id)}
                >
                    M
                </Button>
            </div>

            {/* VUMeter and Fader side by side */}
            <div className="flex h-64 gap-3 items-end mt-4">
                {/* Mock VU Meter driven by volume and random ticks if playing */}
                <VUMeter level={fader.isMuted ? 0 : Math.floor(fader.value * 1.27)} className="h-full w-2" />

                {/* Main Fader */}
                <div className="relative h-full flex items-center justify-center">
                    {/* Fader track scale markings */}
                    <div className="absolute -left-3 inset-y-0 flex flex-col justify-between text-[8px] text-silver-dark font-mono py-2">
                        <span>MAX</span>
                        <span>85</span>
                        <span>70</span>
                        <span>40</span>
                        <span>-âˆž</span>
                    </div>

                    <Slider
                        orientation="vertical"
                        // The Fader state uses 0 to 100 per the user spec
                        min={0}
                        max={100}
                        value={fader.value}
                        onChange={(e) => setFaderValue(id, parseInt(e.target.value))}
                        className="h-full transition-all duration-700 ease-in-out"
                        style={{ WebkitAppearance: 'slider-vertical' } as React.CSSProperties}
                    />
                </div>
            </div>

            {/* Value Readout */}
            <div className="mt-2 text-xs font-mono text-primary bg-carbon px-2 py-1 rounded shadow-inner w-full text-center">
                {fader.value === 0 ? '-inf' : `${fader.value}`}
            </div>
        </div>
    );
}
